import Home from './Home';
import React, { useState } from "react";
import './App.css';

const Todo = ({ list, index, complete, remove }) => {
	return (
		<div className="todo" 
      style={{ textDecoration: list.isCompleted ? "line-through" : "" }}>
      { 
        !isEdit ?
        <div>
          {list.text}
          <div>
            <button onClick={() => edit(index)}>Edit</button>
            <button onClick={() => complete(index)}>Done</button>
            <button onClick={() => remove(index)}>x</button>
          </div>
        </div> : 
        <div>
          <input>{list.text}</input>
          <div>
            <button onClick={() => save(index)}>Save</button>
            {/* <button onClick={() => complete(index)}>Done</button>
            <button onClick={() => remove(index)}>x</button> */}
          </div>
        </div>
      }
    </div> 
  );
}

function TodoForm({ fff }) {
  const [value, setValue] = useState("");

  const handleSubmit = e => {
    e.preventDefault();
    if (!value) return;
    fff(value);
    setValue("");
  };

  return (
    <form onSubmit={handleSubmit}>
      <input
        type="text"
        className="input"
        value={value}
        onChange={e => setValue(e.target.value)}
      />
    </form>
  );
}

function App() {
  const [todos, setTodos] = useState([
    { text: "Learn about React", isCompleted: false, isEdit: false },
    { text: "Meet friend for lunch", isCompleted: false, isEdit: false },
    { text: "Build really cool todo app", isCompleted: false, isEdit: false }
  ]);

  const addTodo = text => {
    const newTodos = [...todos, { text }];
    setTodos(newTodos);
    console.log(...todos, {text});
    console.log(...todos, {text});
  };

  const EditTodo = (e, index) => {
    // const newTodos = [...todos];
    newTodos[index].isEdit = true;
  }

  const completeTodo = index => {
     const newTodos = [...todos];
     newTodos[index].isCompleted = true;
     setTodos(newTodos);
   };

   const removeTodo = index => {
     const newTodos = [...todos];
     newTodos.splice(index, 1);
     setTodos(newTodos);
   };

  return (
    <div className="app">
      <div className="todo-list">
        {todos.map((r, index) => (
          <Todo
            key={index}
            index={index}
            list={r}
            complete={completeTodo}
            remove={removeTodo}
            edit={EditTodo}
          />
        ))}
        <TodoForm fff={addTodo} />
      </div>

      <br />
      <br />
      <br />

      <Home />

    </div>
  );
}

export default App;
